"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";

const slides = [
  {
    title: "Discover Fashion That",
    highlight: "Fits Your Lifestyle",
    description:
      "From simple casual to statement shopping experiences, discover collections to elevate your wardrobe.",
    image: "/icons/hero/hero1.png",
  },
  {
    title: "Upgrade Your",
    highlight: "Daily Style",
    description:
      "Hand-picked collections designed for comfort, confidence, and class.",
    image: "/icons/hero/hero2.png",
  },
  {
    title: "New Season",
    highlight: "New Arrivals",
    description:
      "Stay ahead with trending outfits crafted for modern fashion lovers.",
    image: "/icons/hero/hero3.png",
  },
];

export default function Hero() {
  const [current, setCurrent] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrent((prev) => (prev + 1) % slides.length);
    }, 5000);
    return () => clearInterval(timer);
  }, []);

  return (
    <section className="relative min-h-[80vh] bg-[#faf7f3] overflow-hidden">
      {slides.map((slide, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-700 ${
            index === current ? "opacity-100 z-10" : "opacity-0 z-0"
          }`}
        >
          <div className="max-w-7xl mx-auto grid md:grid-cols-2 items-center h-full px-6 py-20">
            {/* Text */}
            <div>
              <h1 className="text-4xl md:text-5xl font-bold text-gray-900 leading-tight">
                {slide.title}
                <br />
                <span>{slide.highlight}</span>
              </h1>

              <p className="mt-4 text-gray-600 max-w-md">
                {slide.description}
              </p>

              <Link
                href="/products"
                className="inline-block mt-6 bg-black text-white px-6 py-3 text-sm font-semibold"
              >
                Shop Now
              </Link>
            </div>

            {/* Image */}
            <div className="flex justify-center mt-10 md:mt-0">
              <Image
                src={slide.image}
                alt="Hero Image"
                width={500}
                height={600}
                priority={index === 0}
              />
            </div>
          </div>
        </div>
      ))}

      {/* Dots */}
      <div className="absolute bottom-6 left-1/2 -translate-x-1/2 flex gap-2 z-20">
        {slides.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrent(index)}
            className={`h-2 rounded-full transition-all ${
              index === current ? "w-6 bg-black" : "w-2 bg-gray-400"
            }`}
          />
        ))}
      </div>
    </section>
  );
}














